define(function(require) {
	'use strict';
	var SwitcherCss = require('css/cloud-app-switcher.css'),
		SwitcherInterface = require('SwitcherInterface');

	return {
		getSwitcher: function() {
			SwitcherCss.use();
			return SwitcherInterface;
		}
	};

});



