define(function() {

	'use strict';

	/* globals AdfCustomEvent */
	/* globals AdfPage */
	//region dependencies

	//end region

	return function() {
		/**
		 * Call a custom ADF serverListener
		 * @param properties
		 *              source: container that has serveListener,
		 *              listener: listenerName
		 *              //extras.
		 * Properties are sent to the listener.
		 */
		this.execute = function(properties) {
			var source = AdfPage.PAGE.findComponent(properties.source);
			AdfCustomEvent.queue(source, properties.listener, properties, false);
		};
	};
});



