define(function(require) {
	'use strict';

	var ko = require('knockout'),
		$ = require('jquery'),
		Class = require('Class'),
		oj = require('ojs/ojcore'),
		_ = require('underscore'),
		NavigationIconType = require('NavigationIconType'),
		NavigationHelper = require('NavigationHelper');

	/* globals Promise */
	require('promise');
	require('hammerjs');
	require('ojs/ojcore');
	require('ojs/ojknockout');
	require('ojs/ojnavigationlist');
	require('ojs/ojjsontreedatasource');

	/**
	 * Main ViewModel that manages the app navigation switcher
	 * @param triggerEvent event that will toggle the navigation.
	 * @param navigationData Navigation Tree.
	 * @param selectedId [Optional] Default selected link (If nested, the navigationList will expand that branch
	 * and select the link)
	 * @param opened [Optional] specify if the switcher should start as opened.
	 * @param rootLink [Optional] Link to open the root link (home)
	 * @constructor
	 */
	return Class.subClass({}, {
		init: function(triggerEvent, navigationData, selectedId, opened, rootLink) {
			this.triggerEvent = triggerEvent;

			this.selected = ko.observable(selectedId || null);
			this.expanded = ko.observable([]);
			this.dataSource = ko.observable();

			this.rootLink = ko.observable(rootLink);
			this.rootIcon = NavigationIconType().HOME.image;

			this._buildData(navigationData, this.selected());
			this._attachHandlers();

			this.opened = ko.observable(!!opened);
		},

		/**
		 * When The OJ navigation list triggers a selection,
		 * then the navigation is delegated to the action defined in the link
		 * @param event Original Event
		 * @param ui Information on the link clicked
		 */
		onSelect: function(event, ui) {
			//skipOnSelect used to avoid executing an action when changeSelected is called.
			if (!this.skipOnSelect) {
				var elementVM = ko.dataFor(ui.item[0]);
				NavigationHelper.navigate(elementVM.type, elementVM.properties);
			}
		},
		openRoot: function() {
			NavigationHelper.navigate(this.rootLink().type, this.rootLink().properties);
		},
		toggle: function() {
			var self = this;
			return new Promise(function(resolve) {
				if (self.isOpened()) {
					self.close();
				} else {
					self.open();
				}

				//default, if trigger does not work in a browser.
				var timeout = setTimeout(function() {
					resolve();
				}, 1000);

				$('.js-cloud-app-switcher').on('webkitTransitionEnd otransitionend oTransitionEnd msTransitionEnd transitionend',
					function() {
						clearTimeout(timeout);
						resolve();
					});
			});
		},
		open: function() {
			this.opened(true);
		},
		close: function() {
			this.opened(false);
		},
		isOpened: function() {
			return this.opened();
		},
		/**
		 * Listens for the previous navigations, when user clicks on the top toolbar.
		 * If the parent has executeOnBack flag enabled, then it will call the navigation defined on it.
		 * If executeOnBack is false, then normal sliding navigation will happen
		 * @param event triggering event
		 * @param ui ui that has the key of the parent
		 * @returns {boolean} should continue to expand or not.
		 */
		navigateBack: function(event, ui) {
			var node = NavigationHelper.findNodeById(this._navigationData, ui.key).attr;
			if (node.executeOnBack && !this.skipOnSelect) {
				NavigationHelper.navigate(node.type, node.properties);
				setTimeout(function() {
					$(':oj-navigationlist').ojNavigationList('refresh');
				}, 0);
				return false;
			}
			return true;
		},
		changeSelected: function(selectedId) {
			selectedId = selectedId || this.selected();
			if (selectedId !== this.selected()) {
				this.skipOnSelect = true;
				var changed = this._navigateToBranch(this._navigationData, selectedId);
				if (changed) {
					this._changeSelection(selectedId);
				} else {
					this.skipOnSelect = false;
				}
			}
		},
		updateNavigation: function(data, selectedId) {
			this.expanded([]);
			selectedId = selectedId || this.selected();
			this._buildData(data, selectedId);
			this._changeSelection(selectedId);
		},
		_changeSelection: function(selectedId) {
			if (selectedId !== this.selected()) {
				//need to wait until the ojNavigationList is refreshed.
				setTimeout(function() {
					this.skipOnSelect = true;
					this.selected(selectedId);
					//wait until OJ refreshes everything in the UI and enable the onSelect again.
					setTimeout(function() {
						this.skipOnSelect = false;
					}.bind(this));
				}.bind(this), 0);
			}
		},
		_buildData: function(navigationData, selectedId) {
			if (_.isString(navigationData)) {
				//fetch content from URL.
				var self = this;
				$.getJSON(navigationData,
					function(data) {
						self.dataSource(new oj.JsonTreeDataSource(data));
						self._navigateToBranch(data, selectedId);
						self._navigationData = data;
					});
				this.dataSource(new oj.JsonTreeDataSource([]));
			} else {
				this.dataSource(new oj.JsonTreeDataSource(navigationData));
				this._navigateToBranch(navigationData, selectedId);
				this._navigationData = navigationData;
			}
		},
		_registerComponents: function() {
			var componentName = 'cloud-app-switcher';
			if (!ko.components.isRegistered(componentName)) {
				ko.components.register(componentName, {
					viewModel: {
						instance: this
					},
					template: require('cloud-app-switcher-template')
				});
			}
		},
		_attachHandlers: function() {
			$(document).on(this.triggerEvent, function() {
				return this.toggle();
			}.bind(this));
		},
		_navigateToBranch: function(dataSource, selectedId) {
			var path = NavigationHelper.findPath(dataSource, selectedId);
			var pathFound = !_.isEmpty(path);
			if (pathFound) {
				this.expanded(path);
				this.selected.valueHasMutated();
			}
			return pathFound;
		},
		resolveIcon: function(icon) {
			var image;
			if (icon.image) {
				image = icon.image;
			} else {
				var iconType = NavigationIconType()[icon];
				image = iconType ? iconType.image : NavigationIconType().UNKNOWN.image;
			}
			return image;
		}
	});
});



