'use strict';

//region dependencies
import SecurityPolicyType from 'SecurityPolicyType';
import NonePolicy from 'NonePolicy';
import 'promise';

//endregion

export default class TokenPolicy extends NonePolicy {

	constructor(jwtProvider) {
		super(SecurityPolicyType.TOKEN);
		this.jwtProvider = jwtProvider;
	}

	getAuthHeader(params) {
		const self = this;
		return new Promise(function(resolve, reject) {
			self.jwtProvider.getJWT().then(function(jwt) {
				const authHeader = 'Bearer ' + jwt;
				resolve(authHeader);
			}).catch(reject);
		});
	}
}



