define(function(require) {

	'use strict';

	//region dependencies

	var $ = require('jquery');

	//end region

	return function() {
		/**
		 * Call a custom jquery event on document
		 * @param properties
		 *              event: event Name
		 *              //extras.
		 * Properties are sent to the listener.
		 */
		this.execute = function(properties) {
			$(document).trigger(properties.event, properties);
		};
	};
});



