'use strict';

//region dependencies
import SecurityPolicyType from 'SecurityPolicyType';
import 'promise';
//endregion

export default class NonePolicy {

	constructor(value = SecurityPolicyType.NONE) {
		this.value = value;
	}

	beforeAjax() {
		return Promise.resolve();
	}

	getAuthHeader() {
		return Promise.resolve('');
	}
}



