define(function() {

	'use strict';

	//region dependencies

	//end region

	return function() {
		/**
		 * Open a URL in the same tab or in a new tab
		 * @param properties
		 *              target: similar to link target,
		 *              url: url to be opened, can be absolute or relative
		 */
		this.execute = function(properties) {
			var url = properties.url;
			if (properties.target === '_blank') {
				window.open(url);
			} else {
				this._setLocation(url);
			}
		};
		/* istanbul ignore next */
		this._setLocation = function(url) {
			window.location.href = url;
		};
	};
});



