define(function(require) {

	'use strict';

	//region dependencies

	const Class = require('Class'),
		$ = require('jquery');

	/*globals Promise */
	require('promise');
	//end region

	return Class.subClass({}, {

		init: function(policy) {
			this.policy = policy;
		},
		createQuery: function(type, url, params, resolve, reject, data, contentType) {

			const query = {
				type: type,
				url: url,
				dataType: 'json',
				data: data,
				crossDomain: true,
				async: true,
				contentType: contentType,
				xhrFields: {
					withCredentials: true
				},
				success: resolve,
				error: function(xhr, status, text) {
					reject({
						xhr: xhr,
						status: status,
						text: text
					});
				}
			};

			return new Promise((resolve, reject) => {
				this.policy.getAuthHeader(params).then((authHeader) => {
					/* istanbul ignore else */
					if (authHeader && authHeader != '') {
						query.headers = {
							'Authorization': authHeader
						};
					}
					resolve(query);
				}).catch(reject);
			});
		},
		_ajax: function(url, data, params, operation, contentType) {

			return new Promise((resolve, reject) => {
				this.policy.beforeAjax().then(() => {
					this.createQuery(operation, url, params, resolve, reject, data, contentType).then((query) => {
						$.ajax(query);
					}).catch(reject);
				});
			});
		},
		getData: function(url, params, data) {
			return this._ajax(url, data, params, 'GET');
		},
		deleteData: function(url, data, params) {
			return this._ajax(url, data, params, 'DELETE');
		},
		putData: function(url, data, params) {
			return this._ajax(url, data, params, 'PUT');
		},
		postData: function(url, data, params, contentType) {
			return this._ajax(url, data, params, 'POST', contentType);
		}
	});
});



