define(function(require) {

	'use strict';

	//region dependencies

	/* global oj */

	require('ojs/ojrouter');

	//end region

	return function() {
		/**
		 * Sets the current state
		 * @param properties
		 *              event: event Name
		 *              //extras.
		 * Properties are sent to the listener.
		 */
		this.execute = function(properties) {
			oj.Router.rootInstance.stateId(properties.stateId);
		};
	};
});



