define(function(require) {

	'use strict';

	//region dependencies
	const PolicyFactory = require('PolicyFactory').default,
		SuiteServerCommunicationHandler = require('SuiteServerCommunicationHandler');

	//end region

	return function() {

		/**
		 * Open the SUITE HOME URL in the same tab or in a new tab
		 * @param properties
		 *              target: similar to link target,
		 *              url: url to be opened, can be absolute or relative
		 */
		this.execute = function(properties) {
			const NavigationActionFactory = require('NavigationActionFactory');
			const communicationHandler = new SuiteServerCommunicationHandler(PolicyFactory.NONE());
			const NavigationHelper = require('NavigationHelper');
			NavigationHelper.getHomeUrl(properties, communicationHandler)
				.then(function(response) {
					const newProperties = {
						url: response,
						target: properties.target
					};
					NavigationActionFactory.CREATE_URL().execute(newProperties);
				});
		};
	};
});



