'use strict';

//region dependencies
import SecurityPolicyType from 'SecurityPolicyType';
import NonePolicy from 'NonePolicy';
import $ from 'jquery';
//endregion

export default class RemoteCORSPolicy extends NonePolicy {

	constructor(params) {
		super(SecurityPolicyType.REMOTE_CORS);
		this.corsPromise = new Promise(function(resolve) {
			$.getScript(params.appUrl + params.corsInit).then(resolve);
		});
	}

	beforeAjax() {
		return this.corsPromise;
	}
}



