'use strict';

//region dependencies
import NonePolicy from 'NonePolicy';
import TokenPolicy from 'TokenPolicy';
import RemoteCORSPolicy from 'RemoteCORSPolicy';
import $ from 'jquery';
import BasicPolicy from 'BasicPolicy';

//endregion

export default class PolicyFactory {

	static createRemotePolicy(facadeProperties, params) {
		const parameters = $.extend(false, {}, params, {
			jwtProvider: facadeProperties.jwtProvider
		});
		return new PolicyFactory[facadeProperties.source](parameters);
	}

	static NONE() {
		return new NonePolicy();
	}

	static BASIC() {
		return new BasicPolicy();
	}

	static TOKEN(params) {
		return new TokenPolicy(params.jwtProvider);
	}

	static LOCAL(params) {
		return PolicyFactory.TOKEN(params);
	}

	static ASSOCIATED(params) {
		return new RemoteCORSPolicy(params);
	}
}



