'use strict';

//region dependencies
import SecurityPolicyType from 'SecurityPolicyType';
import NonePolicy from 'NonePolicy';
import 'promise';
//endregion

export default class BasicPolicy extends NonePolicy {

	constructor() {
		super(SecurityPolicyType.BASIC);
	}

	getAuthHeader(params) {
		return Promise.resolve('Basic ' + btoa(params.username + ':' + params.password));
	}
}



