define(function(require) {

	'use strict';

	//region dependencies

	var AdfNavigationAction = require('AdfNavigationAction'),
		NoneNavigationAction = require('NoneNavigationAction'),
		JqueryNavigationAction = require('JqueryNavigationAction'),
		OJRouterNavigationAction = require('OJRouterNavigationAction'),
		UrlNavigationAction = require('UrlNavigationAction'),
		HomeNavigationAction = require('HomeNavigationAction');

	//end region

	var createPrefix = 'CREATE_';

	var ActionFactory = {
		createAction: function(type) {
			var factoryFunction = ActionFactory[createPrefix + type] ? ActionFactory[createPrefix + type] : ActionFactory.CREATE_NONE;
			return factoryFunction();
		},
		CREATE_NONE: function() {
			return new NoneNavigationAction();
		},
		CREATE_JQUERY: function() {
			return new JqueryNavigationAction();
		},
		CREATE_URL: function() {
			return new UrlNavigationAction();
		},
		CREATE_ADF: function() {
			return new AdfNavigationAction();
		},
		CREATE_OJ_ROUTER: function() {
			return new OJRouterNavigationAction();
		},
		CREATE_HOME_NAVIGATION: function() {
			return new HomeNavigationAction();
		}
	};
	return ActionFactory;
});



