define(function(require) {

	'use strict';

	//region dependencies

	var NavigationActionFactory = require('NavigationActionFactory'),
		_ = require('underscore');

	//end region

	var NavigationHelper = {
		navigate: function(navigationType, navigationProperties) {
			var action = NavigationActionFactory.createAction(navigationType);
			action.execute(navigationProperties);
		},
		findPath: function(tree, id) {
			var nodes = [];
			_.each(tree, function(node) {
				var nodeId = node.attr.id;
				if (nodeId === id) {
					nodes = [nodeId];
				}
				var sub = NavigationHelper.findPath(node.children, id);
				if (sub.length > 0) {
					nodes = [nodeId].concat(sub);
				}
			});
			return nodes;
		},
		findNodeById: function(tree, id) {
			var foundNode = null;
			_.each(tree, function(node) {
				var nodeId = node.attr.id;
				if (nodeId === id) {
					foundNode = node;
					return false;
				}
				var foundSub = NavigationHelper.findNodeById(node.children, id);
				if (foundSub) {
					foundNode = foundSub;
					return false;
				}
			});
			return foundNode;
		},

		/**
		 *
		 * @param properties containing a default home url
		 * @param communicationHandler where to query for the real home URL
		 * @returns {Promise with the real home URL}
		 */
		getHomeUrl: function(properties, communicationHandler) {
			return new Promise(function(resolve) {
				const navigationUrl = location.origin + '/ic/home/navigation';
				communicationHandler.getData(navigationUrl)
					.then(resolve)
					.catch(() => resolve(properties.url)); //return the default value if the query failed.
			});
		},
	};

	return NavigationHelper;
});



