define(function(require) {
	'use strict';

	var AppSwitcherVM = require('cloud-app-switcher-vm');

	require('ojs/ojbutton');
	require('ojs/ojmenu');
	require('ojs/ojtoolbar');
	require('ojs/ojoffcanvas');
	require('ojs/ojjsontreedatasource');

	/* globals __webpack_public_path__ */

	return {
		/**
		 * Switcher interface, allows to create a navigation switcher.
		 * @param triggerEvent event that will toggle the navigation.
		 * @param data Navigation Tree.
		 * @param selectedId [Optional] Default selected link (If nested, the navigationList will expand that branch
		 * and select the link)
		 * @param opened [Optional] specify if the switcher should start as opened.
		 * @param rootLink [Optional] Link to open the root link (home)
		 * @param publicPath Images public path specified by the consumer
		 * @constructor
		 */
		init: function(triggerEvent, data, selectedId, opened, rootLink, publicPath) {
			this._doInit(triggerEvent, data, selectedId, opened, rootLink, publicPath);
		},

		_doInit: function(triggerEvent, data, selectedId, opened, rootLink, publicPath) {
			/* eslint no-global-assign: 0 */
			if (publicPath) { //noinspection JSAnnotator
				__webpack_public_path__ = publicPath;
			}

			this._appSwitcherVM = new AppSwitcherVM(triggerEvent, data, selectedId, opened, rootLink);
			this._registerComponents();
		},

		/**
		 * Open & Close the navigation Switcher
		 */
		toggle: function() {
			return this._appSwitcherVM.toggle();
		},
		/**
		 * opens the navigation switcher
		 */
		open: function() {
			this._appSwitcherVM.open();
		},
		/**
		 * closes the navigation switcher
		 */
		close: function() {
			this._appSwitcherVM.close();
		},
		/**
		 * Checks if the switcher is opened
		 *  @returns {boolean} is opened?.
		 */
		isOpened: function() {
			return this._appSwitcherVM.isOpened();
		},
		/**
		 * changes selection without triggering any action
		 * @param selectedId The ID of the new selected item.
		 */
		changeSelected: function(selectedId) {
			if (this._appSwitcherVM) {
				this._appSwitcherVM.changeSelected(selectedId);
			}
		},
		/**
		 * Update the current navigation Model.
		 * @param data the new navigation model.
		 * @param selectedId [optional] the new selection (changing the selection will not trigger actions)
		 */
		updateNavigation: function(data, selectedId) {
			this._appSwitcherVM.updateNavigation(data, selectedId);
		},
		_registerComponents: function() {
			this._appSwitcherVM._registerComponents();
		}

	};
});



